<template>
  <div class="cadastro">
    <h1>Cadastro de Empresa - Lança Oferta</h1>
    <form @submit.prevent="cadastrarEmpresa" class="cadastro-form">
      <input 
        type="text" 
        v-model="nome" 
        placeholder="Nome da Empresa" 
        required 
        class="input-field"
      />
      <input 
        type="password" 
        v-model="senha" 
        placeholder="Senha" 
        required 
        class="input-field"
      />
      <input 
        type="text" 
        v-model="localizacao" 
        placeholder="Localização" 
        required 
        class="input-field"
      />
      <button type="submit" class="cadastro-button">Cadastrar</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      nome: '',
      senha: '',
      localizacao: ''
    };
  },
  methods: {
    async cadastrarEmpresa() {
      try {
        await axios.post('https://fabiosantos-lancaofertasapi.hf.space/empresas/', {
          nome: this.nome,
          senha: this.senha,
          localizacao: this.localizacao
        });
        alert('Empresa cadastrada com sucesso!');
      } catch (error) {
        alert('Erro ao cadastrar empresa.');
      }
    }
  }
};
</script>

<style scoped>
.cadastro {
  font-family: Verdana, sans-serif;
  text-align: center;
  padding: 20px;
  max-width: 500px;
  margin: 0 auto;
  background: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
}

.cadastro-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.input-field {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.cadastro-button {
  padding: 12px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.cadastro-button:hover {
  background-color: #45a049;
  transform: translateY(-2px);
}
</style>
