<template>
  <div class="promocoes">
    <h1>Ofertas Disponíveis</h1>
    <input 
      type="text" 
      v-model="query" 
      placeholder="Buscar ofertas" 
      @input="buscarPromocoes" 
      class="search-bar"
    />
    <div class="promocao-lista">
      <div 
        class="promocao-card" 
        v-for="promocao in promocoes" 
        :key="promocao.id"
      >
        <img 
          v-if="promocao.imagem" 
          :src="promocao.imagem" 
          alt="Imagem da Promoção" 
          class="promocao-imagem"
        />
        <h2 class="promocao-titulo">{{ promocao.titulo }}</h2>
        <p class="promocao-descricao">{{ promocao.descricao }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      query: '',
      promocoes: []
    };
  },
  methods: {
    async buscarPromocoes() {
      try {
        const response = await axios.get(`https://fabiosantos-lancaofertasapi.hf.space/promocoes/pesquisa/?query=${this.query}`);
        this.promocoes = response.data;
      } catch (error) {
        alert('Erro ao buscar promoções.');
      }
    }
  },
  async mounted() {
    try {
      const response = await axios.get('https://fabiosantos-lancaofertasapi.hf.space/promocoes/');
      this.promocoes = response.data;
    } catch (error) {
      alert('Erro ao carregar promoções.');
    }
  }
};
</script>

<style scoped>
.promocoes {
  font-family: Verdana, sans-serif;
  text-align: center;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.search-bar {
  width: 80%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.promocao-lista {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.promocao-card {
  background: #f9f9f9;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.promocao-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.promocao-imagem {
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 15px;
  border-radius: 8px;
  object-fit: cover;
}

.promocao-titulo {
  font-size: 20px;
  color: #333;
  margin: 0 0 10px 0;
}

.promocao-descricao {
  font-size: 16px;
  color: #666;
  margin: 0;
}
</style>

