<template>
    <div class="criar-promocao">
      <h1>Criar Nova Promoção</h1>
      <form @submit.prevent="criarPromocao" class="promocao-form">
        <input 
          type="text" 
          v-model="titulo" 
          placeholder="Título da Promoção" 
          required 
          class="input-field"
        />
        <textarea 
          v-model="descricao" 
          placeholder="Descrição da Promoção" 
          required 
          class="input-field"
        ></textarea>
        <input 
          type="file" 
          @change="onFileSelected" 
          class="input-field"
        />
        <button type="submit" class="submit-button">Criar Promoção</button>
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        titulo: '',
        descricao: '',
        imagem: null
      };
    },
    methods: {
      onFileSelected(event) {
        this.imagem = event.target.files[0];
      },
      async criarPromocao() {
        const formData = new FormData();
        formData.append('titulo', this.titulo);
        formData.append('descricao', this.descricao);
        formData.append('imagem', this.imagem);
  
        const empresaId = localStorage.getItem('empresa_id');
        try {
          await axios.post(`https://fabiosantos-lancaofertasapi.hf.space/empresas/${empresaId}/promocoes/`, formData);
          alert('Promoção criada com sucesso!');
          // Resetar o formulário após o sucesso
          this.titulo = '';
          this.descricao = '';
          this.imagem = null;
        } catch (error) {
          console.error('Erro ao criar promoção:', error);
          alert('Erro ao criar promoção');
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .criar-promocao {
    font-family: Verdana, sans-serif;
    text-align: center;
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    background: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .promocao-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .input-field {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .submit-button {
    padding: 12px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .submit-button:hover {
    background-color: #45a049;
    transform: translateY(-2px);
  }
  </style>
  
  