<template>
  <div class="login-container">
    <div class="login-card">
      <h1>Login - Lança Oferta</h1>
      <form @submit.prevent="login" class="login-form">
        <input 
          type="text" 
          v-model="nome" 
          placeholder="Nome da Empresa" 
          required 
          class="input-field"
        />
        <input 
          type="password" 
          v-model="senha" 
          placeholder="Senha" 
          required 
          class="input-field"
        />
        <button type="submit" class="login-button">Entrar</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      nome: '',
      senha: ''
    };
  },
  methods: {
    async login() {
      try {
        const response = await axios.post('https://fabiosantos-lancaofertasapi.hf.space/empresas/login', {
          nome: this.nome,
          senha: this.senha
        });
        localStorage.setItem('empresa_id', response.data.empresa_id); // Salva o ID da empresa no localStorage
        alert(response.data.mensagem);
        this.$router.push('/criar-promocao'); // Redireciona para a página de criação de promoções
      } catch (error) {
        alert('Nome de usuário ou senha incorretos');
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f4; /* Fundo levemente cinza para contrastar com o cartão branco */
  font-family: Verdana, sans-serif;
}

.login-card {
  background-color: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.input-field {
  width: 92%;
  padding: 12px 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.input-field:focus {
  border-color: #4CAF50;
  outline: none;
}

.login-button {
  padding: 12px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.login-button:hover {
  background-color: #45a049;
  transform: translateY(-2px);
}

.login-button:active {
  transform: translateY(0);
}
</style>

  