import { createRouter, createWebHistory } from 'vue-router';
import EmpresaLogin from '../components/EmpresaLogin.vue'; 
import ListaPromocoes from '../components/ListaPromocoes.vue'; 
import EmpresaCadastro from '../components/EmpresaCadastro.vue'; 
import CriarPromocao from '../components/CriarPromocao.vue'; 

const routes = [
  {
    path: '/',
    name: 'Home',
    component: ListaPromocoes // Define a lista de promoções como a rota padrão
  },
  {
    path: '/promocoes',
    name: 'Promocoes',
    component: ListaPromocoes
  },
  {
    path: '/login',
    name: 'Login',
    component: EmpresaLogin
  },
  {
    path: '/cadastro',
    name: 'Cadastro',
    component: EmpresaCadastro
  },
  {
    path: '/criar-promocao',
    name: 'CriarPromocao',
    component: CriarPromocao
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;
